'use client';

import { useState, useEffect, useRef } from 'react';
import Link from 'next/link';
import { usePathname, useRouter } from 'next/navigation';
import { db, type User } from '@/lib/firebase';
import { collection, getDocs, query, orderBy, onSnapshot } from 'firebase/firestore';
import type { Product, Order } from '@/lib/firebase';
import TwoFactorSetup from '@/components/admin/TwoFactorSetup';
import TwoFactorVerify from '@/components/admin/TwoFactorVerify';
import { useAdminAuth } from '@/hooks/useAdminAuth';
import { getAuthClient, signOut } from '@/lib/firebase';

const NAV_ITEMS = [
  { label: 'Dashboard', href: '/admin', icon: '◆' },
  { label: 'Products', href: '/admin/products', icon: '▦' },
  { label: 'Orders', href: '/admin/orders', icon: '⬡' },
  { label: 'Customers', href: '/admin/customers', icon: '◉' },
  { label: 'Analytics', href: '/admin/analytics', icon: '◈' },
  { label: 'Discounts', href: '/admin/discounts', icon: '◇' },
  { label: 'Expenses', href: '/admin/expenses', icon: '▣' },
  { label: 'Alerts', href: '/admin/alerts', icon: '⚠' },
  { label: 'Inventory', href: '/admin/inventory', icon: '▤' },
  { label: 'Waitlist', href: '/admin/waitlist', icon: '◎' },
  { label: 'Messages', href: '/admin/messages', icon: '✉' },
];

export default function AdminLayout({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();
  const router = useRouter();

  // Auth hook - extracts all auth-related state and logic
  const auth = useAdminAuth();

  // Mobile menu
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  // Notifications
  const [notificationsEnabled, setNotificationsEnabled] = useState(false);
  const [lastOrderCount, setLastOrderCount] = useState(0);
  const audioRef = useRef<HTMLAudioElement | null>(null);

  // Pending order count for badge
  const [pendingOrderCount, setPendingOrderCount] = useState(0);
  const [lowStockCount, setLowStockCount] = useState(0);

  // Unread messages count
  const [unreadMessagesCount, setUnreadMessagesCount] = useState(0);

  // Global search
  const [showGlobalSearch, setShowGlobalSearch] = useState(false);
  const [globalSearchQuery, setGlobalSearchQuery] = useState('');
  const [searchProducts, setSearchProducts] = useState<Product[]>([]);
  const [searchOrders, setSearchOrders] = useState<Order[]>([]);

  // Close mobile menu on route change
  useEffect(() => {
    setMobileMenuOpen(false);
  }, [pathname]);

  // Keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if ((e.metaKey || e.ctrlKey) && e.key === 'k') {
        e.preventDefault();
        setShowGlobalSearch(true);
      }
      if (e.key === 'Escape') {
        setShowGlobalSearch(false);
      }
    };
    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, []);

  // Notification permission
  useEffect(() => {
    if (typeof window !== 'undefined' && 'Notification' in window && Notification.permission === 'granted') {
      setNotificationsEnabled(true);
    }
  }, []);

  // Real-time order listener for notifications + badge counts
  useEffect(() => {
    if (!auth.user) return;

    audioRef.current = new Audio('data:audio/wav;base64,');

    const ordersQuery = query(collection(db, 'orders'), orderBy('created_at', 'desc'));
    const unsubscribe = onSnapshot(ordersQuery, (snapshot) => {
      const newOrders = snapshot.docs.map(d => ({ id: d.id, ...d.data() } as Order));

      if (lastOrderCount > 0 && newOrders.length > lastOrderCount) {
        const newOrderCount = newOrders.length - lastOrderCount;
        if (audioRef.current) {
          audioRef.current.play().catch(() => {});
        }
        if (notificationsEnabled && 'Notification' in window) {
          new Notification('New Order!', {
            body: `${newOrderCount} new order${newOrderCount > 1 ? 's' : ''} received`,
            icon: '/favicon.ico',
          });
        }
      }

      setPendingOrderCount(newOrders.filter(o => o.status === 'pending').length);
      setLastOrderCount(newOrders.length);
    }, (error) => {
      console.error('Error listening to orders:', error);
    });

    return () => unsubscribe();
  }, [auth.user, notificationsEnabled, lastOrderCount]);

  // Fetch low stock count for badge
  useEffect(() => {
    if (!auth.user) return;
    const fetchLowStock = async () => {
      try {
        const productsSnap = await getDocs(collection(db, 'products'));
        const productsData = productsSnap.docs.map(d => ({ id: d.id, ...d.data() } as Product));
        const sizes = ['XS', 'S', 'M', 'L', 'XL', 'XXL'];
        let count = 0;
        productsData.forEach(p => {
          const colors = p.colors || [];
          if (colors.length > 0) {
            colors.forEach((color) => {
              sizes.forEach(size => {
                if (((color.stock as Record<string, number>)?.[size] || 0) < 5) count++;
              });
            });
          } else {
            sizes.forEach(size => {
              if (((p.stock as Record<string, number>)?.[size] || 0) < 5) count++;
            });
          }
        });
        setLowStockCount(count);
      } catch (err) {
        console.error('Error fetching low stock:', err);
      }
    };
    fetchLowStock();
  }, [auth.user]);

  // Fetch unread messages count for badge
  useEffect(() => {
    if (!auth.user) return;
    const fetchUnreadMessages = async () => {
      try {
        const messagesSnap = await getDocs(collection(db, 'contact_messages'));
        const unread = messagesSnap.docs.filter(d => d.data().read !== true).length;
        setUnreadMessagesCount(unread);
      } catch (err) {
        console.error('Error fetching unread messages:', err);
      }
    };
    fetchUnreadMessages();
  }, [auth.user]);

  // Fetch search data when global search opens
  useEffect(() => {
    if (!showGlobalSearch || !auth.user) return;
    const fetchSearchData = async () => {
      try {
        const [productsSnap, ordersSnap] = await Promise.all([
          getDocs(collection(db, 'products')),
          getDocs(query(collection(db, 'orders'), orderBy('created_at', 'desc'))),
        ]);
        setSearchProducts(productsSnap.docs.map(d => ({ id: d.id, ...d.data() } as Product)));
        setSearchOrders(ordersSnap.docs.map(d => ({ id: d.id, ...d.data() } as Order)));
      } catch (err) {
        console.error('Error fetching search data:', err);
      }
    };
    fetchSearchData();
  }, [showGlobalSearch, auth.user]);

  const enableNotifications = async () => {
    if ('Notification' in window) {
      const permission = await Notification.requestPermission();
      if (permission === 'granted') setNotificationsEnabled(true);
    }
  };

  const isActive = (href: string) => {
    if (href === '/admin') return pathname === '/admin';
    return pathname.startsWith(href);
  };

  // Auth loading state
  if (auth.authLoading) {
    return (
      <div className="min-h-screen bg-black text-white flex items-center justify-center">
        <div className="text-center">
          <div className="w-12 h-12 border-2 border-white/20 border-t-white rounded-full animate-spin mx-auto mb-4" />
          <p className="text-white/40 text-sm tracking-wider uppercase">Initializing DYMNDS OS</p>
        </div>
      </div>
    );
  }

  // Login form
  // SEC-003: Gate on mfaSetupPending so dashboard never briefly renders
  // between Firebase auth completing and the 2FA prompt appearing.
  if (!auth.user || auth.mfaSetupPending) {
    // Show 2FA setup prompt after successful login
    if (auth.show2FASetup) {
      return (
        <div className="min-h-screen bg-black text-white flex items-center justify-center px-4">
          <TwoFactorSetup
            user={auth.pendingMFAUser!}
            onComplete={() => {
              // SEC-002/003: Clear MFA pending state, allow dashboard
              auth.setMfaSetupPending(false);
              auth.setShow2FASetup(false);
              auth.setPendingMFAUser(null);
              router.push('/admin');
            }}
            onSkip={async () => {
              // SEC-002/003: Clear MFA pending state before logout
              auth.setMfaSetupPending(false);
              auth.setShow2FASetup(false);
              auth.setPendingMFAUser(null);
              // Log out — 2FA setup is required for admin access
              await fetch('/api/auth/session', { method: 'DELETE' }).catch(() => {});
              const authClient = getAuthClient();
              if (authClient) await signOut(authClient);
            }}
          />
        </div>
      );
    }

    // Show MFA verification prompt
    if (auth.showMFA && auth.mfaError) {
      return (
        <div className="min-h-screen bg-black text-white flex items-center justify-center px-4">
          <TwoFactorVerify
            error={auth.mfaError}
            onSuccess={() => {
              auth.setShowMFA(false);
              auth.setMfaError(null);
              // Complete login after MFA verification
              // SEC-010: Include CSRF token header
              const authClient = getAuthClient();
              if (authClient?.currentUser) {
                authClient.currentUser.getIdToken().then((idToken) => {
                  const csrfTok = document.cookie
                    .split('; ')
                    .find(row => row.startsWith('csrf_token='))
                    ?.split('=')[1] || '';
                  fetch('/api/auth/session', {
                    method: 'POST',
                    headers: {
                      'Content-Type': 'application/json',
                      ...(csrfTok && { 'X-CSRF-Token': csrfTok }),
                    },
                    body: JSON.stringify({ idToken }),
                  }).catch(() => {});
                });
              }
            }}
            onCancel={() => {
              auth.setShowMFA(false);
              auth.setMfaError(null);
              auth.setLoginError('MFA verification cancelled');
            }}
          />
        </div>
      );
    }

    return (
      <div className="min-h-screen bg-black text-white flex items-center justify-center px-4">
        <div className="w-full max-w-md">
          <div className="text-center mb-10">
            <div className="inline-flex items-center justify-center w-16 h-16 border border-white/10 rounded-2xl mb-6 bg-white/[0.03]">
              <span className="text-2xl">◆</span>
            </div>
            <h1 className="text-5xl font-bebas tracking-wider mb-2">DYMNDS OS</h1>
            <p className="text-white/30 text-sm tracking-widest uppercase">Admin Access</p>
          </div>

          <form onSubmit={auth.handleLogin} className="bg-white/[0.03] border border-white/[0.08] rounded-2xl p-8 space-y-6">
            {auth.loginError && (
              <div className="p-4 bg-red-500/10 border border-red-500/20 rounded-xl text-red-400 text-sm">
                {auth.loginError}
              </div>
            )}

            <div>
              <label htmlFor="admin-email" className="block text-xs uppercase tracking-widest text-white/30 mb-2">Email</label>
              <input
                id="admin-email"
                type="email"
                value={auth.email}
                onChange={(e) => auth.setEmail(e.target.value)}
                className="input-premium w-full bg-black/50 border border-white/[0.12] rounded-xl px-4 py-3.5 text-white placeholder:text-white/20 focus:outline-none"
                placeholder="admin@weardymnds.com"
                required
              />
            </div>

            <div>
              <label htmlFor="admin-password" className="block text-xs uppercase tracking-widest text-white/30 mb-2">Password</label>
              <input
                id="admin-password"
                type="password"
                value={auth.password}
                onChange={(e) => auth.setPassword(e.target.value)}
                className="input-premium w-full bg-black/50 border border-white/[0.12] rounded-xl px-4 py-3.5 text-white placeholder:text-white/20 focus:outline-none"
                placeholder="••••••••"
                required
              />
            </div>

            <button
              type="submit"
              disabled={auth.loginLoading}
              className="btn-premium w-full py-4 bg-white text-black font-bold tracking-wider uppercase rounded-xl hover:bg-white/90 transition-all disabled:opacity-50 flex items-center justify-center gap-2"
            >
              {auth.loginLoading ? (
                <>
                  <div className="w-4 h-4 border-2 border-black/30 border-t-black rounded-full animate-spin" />
                  Authenticating...
                </>
              ) : (
                'Sign In'
              )}
            </button>
          </form>

          <p className="text-center text-white/15 text-xs mt-8 tracking-widest uppercase">Pressure Creates Diamonds</p>
        </div>
      </div>
    );
  }

  // Authenticated layout
  return (
    <div className="min-h-screen bg-black text-white">
      {/* Mobile Header */}
      <header className="lg:hidden fixed top-0 left-0 right-0 h-16 bg-neutral-950/95 backdrop-blur-xl border-b border-white/[0.06] z-50 flex items-center justify-between px-4">
        <h1 className="text-xl font-bebas tracking-wider">DYMNDS OS</h1>
        <div className="flex items-center gap-3">
          <button
            onClick={() => setShowGlobalSearch(true)}
            className="p-2 rounded-lg bg-white/[0.05] hover:bg-white/[0.1] transition-colors"
            title="Search (⌘K)"
            aria-label="Search products, orders, and customers"
          >
            <span className="text-white/50">⌕</span>
          </button>
          <button
            onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
            className="p-2 rounded-lg bg-white/[0.05] hover:bg-white/[0.1] transition-colors"
            aria-label={mobileMenuOpen ? 'Close admin menu' : 'Open admin menu'}
            aria-expanded={mobileMenuOpen}
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              {mobileMenuOpen ? (
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              ) : (
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              )}
            </svg>
          </button>
        </div>
      </header>

      {/* Mobile Menu Overlay */}
      {mobileMenuOpen && (
        <div
          className="lg:hidden fixed inset-0 bg-black/60 backdrop-blur-sm z-40"
          onClick={() => setMobileMenuOpen(false)}
        />
      )}

      {/* Sidebar */}
      <aside className={`fixed left-0 top-0 h-full w-64 bg-neutral-950 border-r border-white/[0.06] z-50 transform transition-transform duration-300 ease-[cubic-bezier(0.16,1,0.3,1)] lg:translate-x-0 ${mobileMenuOpen ? 'translate-x-0' : '-translate-x-full'}`}>
        {/* Logo */}
        <div className="p-6 border-b border-white/[0.06]">
          <div className="flex items-center gap-3">
            <span className="text-xl">◆</span>
            <div>
              <h1 className="text-xl font-bebas tracking-wider">DYMNDS OS</h1>
              <p className="text-white/25 text-[10px] tracking-widest uppercase">Command Center</p>
            </div>
          </div>
        </div>

        {/* Search trigger */}
        <div className="px-4 pt-4">
          <button
            onClick={() => { setShowGlobalSearch(true); setMobileMenuOpen(false); }}
            className="w-full flex items-center gap-3 px-3 py-2.5 bg-white/[0.03] border border-white/[0.06] rounded-xl text-white/30 text-sm hover:bg-white/[0.05] transition-colors"
          >
            <span>⌕</span>
            <span>Search...</span>
            <span className="ml-auto text-[10px] bg-white/[0.06] px-1.5 py-0.5 rounded">⌘K</span>
          </button>
        </div>

        {/* Nav Items */}
        <nav aria-label="Admin navigation" className="px-3 pt-4 space-y-1 overflow-y-auto" style={{ maxHeight: 'calc(100vh - 220px)' }}>
          {NAV_ITEMS.map((item) => {
            const active = isActive(item.href);
            const badge = item.label === 'Orders' && pendingOrderCount > 0
              ? pendingOrderCount
              : item.label === 'Alerts' && lowStockCount > 0
              ? lowStockCount
              : item.label === 'Messages' && unreadMessagesCount > 0
              ? unreadMessagesCount
              : null;

            return (
              <Link
                key={item.href}
                href={item.href}
                onClick={() => setMobileMenuOpen(false)}
                className={`flex items-center gap-3 px-4 py-3 rounded-xl text-sm tracking-wider uppercase transition-all duration-200 ${
                  active
                    ? 'bg-white text-black font-medium'
                    : 'text-white/40 hover:text-white/70 hover:bg-white/[0.04]'
                }`}
              >
                <span className={`text-base ${active ? 'opacity-100' : 'opacity-40'}`}>{item.icon}</span>
                <span className="flex-1">{item.label}</span>
                {badge && (
                  <span className={`px-2 py-0.5 text-xs rounded-full ${
                    active ? 'bg-black/20 text-black' : 'bg-red-500/80 text-white'
                  }`}>
                    {badge}
                  </span>
                )}
              </Link>
            );
          })}
        </nav>

        {/* Bottom section */}
        <div className="absolute bottom-0 left-0 right-0 p-4 border-t border-white/[0.06] space-y-2">
          {/* Notification toggle */}
          {'Notification' in (typeof window !== 'undefined' ? window : {}) && (
            <button
              onClick={enableNotifications}
              className={`w-full flex items-center gap-3 px-4 py-2.5 rounded-xl text-sm transition-all ${
                notificationsEnabled
                  ? 'text-green-400/70 bg-green-500/[0.05]'
                  : 'text-white/30 hover:text-white/50 hover:bg-white/[0.03]'
              }`}
            >
              <span>{notificationsEnabled ? '●' : '○'}</span>
              <span className="tracking-wider uppercase text-xs">{notificationsEnabled ? 'Alerts On' : 'Enable Alerts'}</span>
            </button>
          )}

          {/* User + Logout */}
          <div className="flex items-center gap-3 px-4 py-2.5">
            <div className="w-7 h-7 rounded-full bg-white/[0.06] flex items-center justify-center text-xs text-white/40">A</div>
            <div className="flex-1 min-w-0">
              <p className="text-xs text-white/30 truncate">{auth.user?.email}</p>
            </div>
            <button
              onClick={auth.handleLogout}
              className="text-xs text-red-400/60 hover:text-red-400 transition-colors tracking-wider uppercase"
            >
              Exit
            </button>
          </div>
        </div>
      </aside>

      {/* Main Content */}
      <main className="lg:ml-64 min-h-screen pt-20 lg:pt-0">
        <div className="p-4 lg:p-8">
          {children}
        </div>
      </main>

      {/* Global Search Modal */}
      {showGlobalSearch && (
        <div className="fixed inset-0 bg-black/80 backdrop-blur-sm z-[60] flex items-start justify-center pt-24 lg:pt-32 p-4">
          <div className="modal-panel bg-neutral-950 border border-white/[0.08] rounded-2xl w-full max-w-2xl overflow-hidden">
            <div className="p-4 border-b border-white/[0.06] flex items-center gap-3">
              <span className="text-white/30">⌕</span>
              <input
                type="text"
                autoFocus
                value={globalSearchQuery}
                onChange={(e) => setGlobalSearchQuery(e.target.value)}
                placeholder="Search products, orders, customers..."
                className="flex-1 bg-transparent text-lg focus:outline-none text-white placeholder:text-white/20"
                aria-label="Search products, orders, and customers"
              />
              <button
                onClick={() => { setShowGlobalSearch(false); setGlobalSearchQuery(''); }}
                className="px-2 py-1 bg-white/[0.06] rounded text-xs text-white/30 hover:bg-white/[0.1] transition-colors"
                aria-label="Close search"
              >
                ESC
              </button>
            </div>

            <div className="max-h-96 overflow-y-auto">
              {globalSearchQuery.length < 2 ? (
                <div className="p-8 text-center text-white/30">
                  <p className="mb-2">Start typing to search...</p>
                  <p className="text-sm text-white/20">Products, orders, customer emails</p>
                </div>
              ) : (
                <>
                  {searchProducts.filter(p =>
                    p.title?.toLowerCase().includes(globalSearchQuery.toLowerCase()) ||
                    p.slug?.toLowerCase().includes(globalSearchQuery.toLowerCase())
                  ).length > 0 && (
                    <div className="p-2">
                      <p className="px-3 py-2 text-[10px] uppercase tracking-widest text-white/25">Products</p>
                      {searchProducts
                        .filter(p =>
                          p.title?.toLowerCase().includes(globalSearchQuery.toLowerCase()) ||
                          p.slug?.toLowerCase().includes(globalSearchQuery.toLowerCase())
                        )
                        .slice(0, 5)
                        .map(product => (
                          <button
                            key={product.id}
                            onClick={() => {
                              setShowGlobalSearch(false);
                              setGlobalSearchQuery('');
                              router.push('/admin/products');
                            }}
                            className="w-full text-left px-3 py-3 rounded-xl hover:bg-white/[0.04] flex items-center gap-3 transition-colors"
                          >
                            <span className="text-white/20">▦</span>
                            <div className="flex-1 min-w-0">
                              <p className="font-medium truncate">{product.title}</p>
                              <p className="text-sm text-white/30">${product.price} · {product.category}</p>
                            </div>
                          </button>
                        ))}
                    </div>
                  )}

                  {searchOrders.filter(o =>
                    o.id?.toLowerCase().includes(globalSearchQuery.toLowerCase()) ||
                    o.customer_name?.toLowerCase().includes(globalSearchQuery.toLowerCase()) ||
                    o.customer_email?.toLowerCase().includes(globalSearchQuery.toLowerCase())
                  ).length > 0 && (
                    <div className="p-2 border-t border-white/[0.04]">
                      <p className="px-3 py-2 text-[10px] uppercase tracking-widest text-white/25">Orders</p>
                      {searchOrders
                        .filter(o =>
                          o.id?.toLowerCase().includes(globalSearchQuery.toLowerCase()) ||
                          o.customer_name?.toLowerCase().includes(globalSearchQuery.toLowerCase()) ||
                          o.customer_email?.toLowerCase().includes(globalSearchQuery.toLowerCase())
                        )
                        .slice(0, 5)
                        .map(order => (
                          <button
                            key={order.id}
                            onClick={() => {
                              setShowGlobalSearch(false);
                              setGlobalSearchQuery('');
                              router.push('/admin/orders');
                            }}
                            className="w-full text-left px-3 py-3 rounded-xl hover:bg-white/[0.04] flex items-center gap-3 transition-colors"
                          >
                            <span className="text-white/20">⬡</span>
                            <div className="flex-1 min-w-0">
                              <p className="font-medium">#{order.id.slice(-6).toUpperCase()}</p>
                              <p className="text-sm text-white/30">{order.customer_name} · ${order.total_amount?.toFixed(2)}</p>
                            </div>
                          </button>
                        ))}
                    </div>
                  )}

                  {(() => {
                    const customers = Array.from(new Set(searchOrders.map(o => o.customer_email)));
                    const filtered = customers.filter(email =>
                      email?.toLowerCase().includes(globalSearchQuery.toLowerCase()) ||
                      searchOrders.find(o => o.customer_email === email)?.customer_name?.toLowerCase().includes(globalSearchQuery.toLowerCase())
                    );
                    return filtered.length > 0 ? (
                      <div className="p-2 border-t border-white/[0.04]">
                        <p className="px-3 py-2 text-[10px] uppercase tracking-widest text-white/25">Customers</p>
                        {filtered.slice(0, 5).map(email => {
                          const customer = searchOrders.find(o => o.customer_email === email);
                          const orderCount = searchOrders.filter(o => o.customer_email === email).length;
                          return (
                            <button
                              key={email}
                              onClick={() => {
                                setShowGlobalSearch(false);
                                setGlobalSearchQuery('');
                                router.push('/admin/customers');
                              }}
                              className="w-full text-left px-3 py-3 rounded-xl hover:bg-white/[0.04] flex items-center gap-3 transition-colors"
                            >
                              <span className="text-white/20">◉</span>
                              <div className="flex-1 min-w-0">
                                <p className="font-medium">{customer?.customer_name || 'Unknown'}</p>
                                <p className="text-sm text-white/30">{email} · {orderCount} orders</p>
                              </div>
                            </button>
                          );
                        })}
                      </div>
                    ) : null;
                  })()}

                  {searchProducts.filter(p =>
                    p.title?.toLowerCase().includes(globalSearchQuery.toLowerCase()) ||
                    p.slug?.toLowerCase().includes(globalSearchQuery.toLowerCase())
                  ).length === 0 &&
                  searchOrders.filter(o =>
                    o.id?.toLowerCase().includes(globalSearchQuery.toLowerCase()) ||
                    o.customer_name?.toLowerCase().includes(globalSearchQuery.toLowerCase()) ||
                    o.customer_email?.toLowerCase().includes(globalSearchQuery.toLowerCase())
                  ).length === 0 && (
                    <div className="p-8 text-center text-white/25">
                      No results for &ldquo;{globalSearchQuery}&rdquo;
                    </div>
                  )}
                </>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
